	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Heroes and Villains","date":"16 Jun 2006","slug":"/2006-06-16-heroes-and-villains/","tags":"Y2006"},"html":"<p>The thing that's so cool about the World Cup, and soccer in general, is that it's so blatantly emotional and subjective. </p>\n<p>Take the past couple of days. There have been 3 games (Germany - Poland, England - Trinidad and Tobago, Sweden - Paraguay) where the plot line has been basically the same. The favorite struggles to score against a defensive underdog and can only win by scoring very late. </p>\n<p>In the first, the <a href=\"http://football.guardian.co.uk/worldcup2006/matchreport/0,,1797918,00.html\" title=\"Guardian Unlimited Football | World Cup 2006 | Heartbreak for Poles as Neuville grabs last-gasp winner\">heroes</a> were persistent and courageous, and the result was a just reward for tenaciousness and hard work.</p>\n<p>In the second, the <a href=\"http://www.dw-world.de/dw/article/0,2144,2057125,00.html\" title=\"England Struggle to 2-0 Win Against Trinidad and Tobago | Match Reports | Deutsche Welle | 15.06.2006\">villains</a> were confused and inept, and the result was simply a great stroke of luck.</p>\n<p>The <a href=\"http://www.nytimes.com/iht/2006/06/16/sports/IHT-16websweden.html\" title=\"Sweden 1, Paraguay 0: Big-Name Attack Struggles but Survives on Late Strike - New York Times\">third</a> was so boring that I switched it off.</p>"},"next":{"fields":{"title":"USA - Czech Republic","date":"12 Jun 2006","slug":"/2006-06-12-usa-czech-republic/"}},"previous":{"fields":{"title":"VIP for a day at the World Cup","date":"20 Jun 2006","slug":"/2006-06-20-vip-for-a-day-at-the-world-cup/"}}},{"node":{"fields":{"title":"USA - Czech Republic","date":"12 Jun 2006","slug":"/2006-06-12-usa-czech-republic/","tags":"Y2006"},"html":"<p>So, the real World Cup started at 6 pm today, but the USA is being outclassed by the Czech Republic. The US can control the ball, but it looks like they've made the mistake of bringing stiletto knives to an axe fight. They're trailing 2:0 at halftime. At least they've mastered the offside trap, which the Germans still need to work on.</p>\n<p>Germans have this thing about complicated football (er, soccer) pools, so you can see how badly I've predicted at <a href=\"http://www.kicktipp.de/blogtipp/tippuebersicht\">Blogtipp</a>. I did pick the Czech Republic to win the Cup, based on two simulations I read about. One was a video game, the other a beer competition in Stern. Good enough for me.</p>\n<p>The best Internet live commentary, by the way, is at the <a href=\"http://football.guardian.co.uk/worldcup2006\">Guardian</a>. <strong>Update:</strong> and not just because they <a href=\"http://football.guardian.co.uk/worldcup2006/minbymin/0,,1788207,00.html\">quote</a> my emails (scroll down to 83 minutes).</p>\n<p>Conclusion: 3:0. This has been a Cup without blowouts, until now. The US was energetic, but unimaginative. How did the US ever earn the <a href=\"http://www.fifa.com/en/mens/statistics/index/0,2548,All-May-2006,00.html\">5th place FIFA ranking</a>? In March against Germany, and again today, we've been outclassed. </p>"},"next":{"fields":{"title":"From the World Cup Front","date":"08 Jun 2006","slug":"/2006-06-08-from-the-world-cup-front/"}},"previous":{"fields":{"title":"Heroes and Villains","date":"16 Jun 2006","slug":"/2006-06-16-heroes-and-villains/"}}},{"node":{"fields":{"title":"From the World Cup Front","date":"08 Jun 2006","slug":"/2006-06-08-from-the-world-cup-front/","tags":"Y2006"},"html":"<p>MamaMaus reports from Munich. She's at a 2 star hotel with 5 star World Cup rates this week for business meetings before the World Cup opener on Friday. The hotel was apparently was last redecorated for the previous big sports event in Munich, the 1972 Olympics. Her meeting for Thursday was cancelled, she booked a flight home so she can sleep in her own bed one night before returning for the opening match and the hotel lost in the '70s Friday night. She may have VIP tickets, but her hotel is lost in the twilight zone.</p>"},"next":{"fields":{"title":"Never Talk to Tele2","date":"06 Jun 2006","slug":"/2006-06-06-never-talk-to-tele2/"}},"previous":{"fields":{"title":"USA - Czech Republic","date":"12 Jun 2006","slug":"/2006-06-12-usa-czech-republic/"}}}],"pathPrefix":"","first":false,"last":false,"index":214,"pageCount":1101}}

